<template>
  <div class="site-list">
    <div class="site-list-process"></div>
    <div class="site-list-content">
      <div class="site-list-content--name">
        <span class="site-list-content--name-start">【{{ item.start.time }}】</span>
        <span>{{ item.start.name }}</span>
      </div>
      <div class="site-list-content--tag">
        <van-tag :type="tagName">{{ item.vehicle.type }}</van-tag>
        <van-tag type="primary">{{ item.vehicle.id }}</van-tag>
        <van-tag plain type="primary" @touchstart="handleOpenDetail">详情</van-tag>
      </div>
      <div class="site-list-content--middle">
        <collapse v-show="item.middle.length" :data="item.middle"/>
      </div>
      <div class="site-list-content--name">
        <span class="site-list-content--name-end">【{{ item.end.time }}】</span>
        <span>{{ item.end.name }}</span>
      </div>
    </div>
    <button class="site-list-jump" v-if="jump">
      <van-icon color="#3962E8" name="location" size="40" @touchstart="handleJumpDetail"/>
    </button>
    <van-dialog v-model="show" title="车辆详情">
      <div class="card-detail">
        <ul>
          <li class="van-hairline--bottom">
            <span>车牌号</span>
            <span>{{ item.vehicle.id }}</span>
          </li>
          <li class="van-hairline--bottom">
            <span>司机姓名</span>
            <span>{{ item.vehicle.name }}</span>
          </li>
          <li class="van-hairline--bottom">
            <span>手机号</span>
            <span>{{ item.vehicle.phone }}</span>
          </li>
          <li>
            <span>备注</span>
            <div class="remarks">
              <span>{{ item.vehicle.remarks }}</span>
            </div>
          </li>
        </ul>
      </div>
    </van-dialog>
  </div>
</template>

<script>
  import collapse from '@/components/base/collapse'
  import { mapMutations } from 'vuex'
  import { SET_ROUTER_MODE, SET_CURRENT_SITE } from '@/store/Types/index'

  export default {
    name: 'site-list',
    components: {
      collapse
    },
    props: {
      item: {
        type: Object,
        require: true
      },
      jump: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        show: false
      }
    },
    computed: {
      tagName() {
        return this.item.vehicle.type === '工作日' ? 'primary' : 'warning'
      }
    },
    methods: {
      ...mapMutations([SET_ROUTER_MODE, SET_CURRENT_SITE]),
      handleOpenDetail(e) {
        e.preventDefault()

        this.show = true
      },
      handleJumpDetail() {
        const { SET_ROUTER_MODE, SET_CURRENT_SITE } = this
        SET_ROUTER_MODE('page-go')
        SET_CURRENT_SITE(this.item)
        this.$router.push({
          name: 'line'
        })
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~@/styles/src/colors";

  @w: 16px;

  .round() {
    content: '';
    width: @w - 4px;
    height: @w - 4px;
    border-radius: 50%;
    background-color: white;
  }

  .site-list {
    position: relative;
    display: flex;
    justify-content: start;
    padding: 14px 30px 14px 10px;

    &-jump {
      position: absolute;
      right: 0;
      top: 8px;
      background-color: transparent;
      border: none;
    }

    &-process {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: @w;
      padding: 2px;
      border-radius: @w;
      background-color: @primary;
      margin-right: 20px;

      &:before {
        .round()
      }

      &:after {
        .round()
      }
    }

    &-content {
      font-size: 16px;

      &--name {
        font-size: 16px;
        font-weight: bold;
        text-indent: -10px;

        &-start {
          color: @tag-blue;
        }

        &-end {
          color: @tag-pink;
        }
      }

      &--tag {
        margin-top: 6px;
        span + span {
          margin-left: 4px;
        }
      }

      &--middle {
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }

  }

  .card-detail {
    margin-top: 20px;

    ul {
      padding: 0 16px;

      li {
        display: flex;
        margin-bottom: 10px;

        span:first-child {
          color: @primary-text;
          word-break: keep-all;
        }
        span:last-child {
          position: absolute;
          left: 92px;
          color: @normal-text;
        }

        .remarks {
          position: relative;
          left: 60px;
          width: calc(100% - 90px);
          span {
            position: relative;
            left: 0;
            word-break: normal;
          }
        }
      }
    }
  }
</style>
