<template>
  <div class="v-easy-collapse">
    <div class="v-easy-collapse-control" @touchstart="open = !open">
      <van-icon :class="iconClass" name="arrow-down" />
      <span>{{ data.length }}站</span>
    </div>
    <div class="v-easy-collapse-content">
      <transition name="collapse" mode="out-in">
        <ul v-show="open" :style="{height: data.length * 24 + 'px'}">
          <li v-for="(item, index) in data" :key="index">
            <span>【{{ item.time }}】</span>
            <span>{{ item.name }}</span>
          </li>
        </ul>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'collapse',
    props: {
      data: {
        type: Array,
        require: true
      }
    },
    data() {
      return {
        open: false
      }
    },
    computed: {
      iconClass() {
        return ['arrow', this.open ? 'arrow-down' : '']
      }
    },
    methods: {
    }
  }
</script>

<style scoped lang="less">

  .v-easy-collapse {

    .arrow {
      transition: transform .2s;
    }
    .arrow-down {
      transform: rotateZ(180deg);
    }

    &-control {
      display: flex;
      align-items: center;
      span {
        margin-left: 6px;
        cursor: pointer;
      }
    }

    &-content {
      width: calc(100% + 36px);
      margin-top: 4px;
      margin-left: -36px;

      ul {
        overflow: hidden;

        li {
          position: relative;

          span:first-child {
            margin-left: 56px;
          }
          &:after {
            content: '';
            position: absolute;
            left: 3px;
            top: 8px;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: white;
          }
        }
      }
    }
  }
</style>
